body{margin:0;font-family:-apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace}.tooltip{margin-left:8px;display:inline-block;text-align:center;background-color:darkgrey;border-radius:50%;width:18px;height:18px;line-height:18px;cursor:default}.tooltip::before{content:'?';color:black;text-shadow:none}.tooltip .tooltip-text{visibility:hidden;width:280px;background-color:rgba(0,0,0,0.7);color:#fff;text-align:center;padding:0.5rem;border-radius:0.5rem;position:absolute;z-index:1}.tooltip:hover .tooltip-text{visibility:visible}.group-content{opacity:0;height:0;margin:0px 1rem;overflow:hidden;-webkit-transition:0.5s;transition:0.5s;-webkit-transition-timing-function:cubic-bezier(0.05, 0.69, 0.14, 1);transition-timing-function:cubic-bezier(0.05, 0.69, 0.14, 1)}.group-content.open{opacity:1;height:auto;-webkit-transition:0.5s;transition:0.5s;-webkit-transition-timing-function:cubic-bezier(0.05, 0.69, 0.14, 1);transition-timing-function:cubic-bezier(0.05, 0.69, 0.14, 1)}.group-container svg{-webkit-transform:rotate(90deg);transform:rotate(90deg)}.group-container.open svg{-webkit-transform:rotate(-90deg);transform:rotate(-90deg)}.group-heading{display:flex;align-items:center;border-bottom:2px solid rgba(255,255,255,0.8)}.group-title{display:inherit;font-size:2rem;color:white;margin-right:0.5rem}.group-button{fill:white}.control-panel{position:absolute;top:0px;left:0px;margin:0 auto;display:block;width:48rem;height:100vh;background-color:rgba(0,0,0,0.7);border-right:5px solid #aaa;overflow:hidden}.panel{padding:20px;-webkit-transition:0.3s;transition:0.3s;-webkit-transition-timing-function:cubic-bezier(0.05, 0.69, 0.14, 1);transition-timing-function:cubic-bezier(0.05, 0.69, 0.14, 1)}.panel-open{overflow-y:scroll}.panel-closed{-webkit-transform:translate(-95%);transform:translate(-95%)}.panel-toggle{position:relative;background-color:black;color:white;border-radius:2rem;font-size:2rem;width:60px;height:30px;top:calc(calc(100vh / 2) - 15px);left:calc(100% - 10px)}.field-container{margin:10px 0px;color:white;font-size:1.6rem;text-shadow:2px 2px black}.panel-input{font-size:1.6rem;margin-left:1rem;padding:0.3rem 0.6rem;border-radius:0.7rem;background:rgba(1,1,1,0.7);color:white;text-shadow:2px 2px black}.point-control-btn{width:5rem;padding:0.5rem}.poly-container{padding-left:2rem;font-size:1.6rem;color:white}.poly-term sup{vertical-align:super;font-size:0.75em}.pause-button{background:rgba(1,1,1,0.4);text-shadow:2px 2px black;color:white;border-radius:8px;border:2px solid white;margin:0 auto;display:block;font-size:2rem;height:6rem;width:20rem}.preset-selector{width:25rem}.frametime{display:block;margin:auto;margin-top:10rem;text-align:center;font-size:2rem;margin-bottom:0.5rem;color:white;text-shadow:2px 2px black}

*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}body{box-sizing:border-box;width:100%}.glcanvas{position:absolute;display:block;margin:0 auto;z-index:-10;height:100vh;width:100%}

